import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'app-menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.css']
})

export class MenuComponent {
    @Output() onCancel = new EventEmitter<any>();
    onMenuClose() {
        this.onCancel.emit('');
    }
}
